@import url('https://fonts.googleapis.com/css2?family=Readex+Pro:wght@400;600;700&display=swap');
.h2_center{
    text-align: center;
    color: #fff;
    font-size: 23px;
    font-weight: bold;
    font-family: 'Readex Pro';
}


.box_changes{
    background:#0d1321;
    border-radius: 10px;
    margin: 10px;
    margin-bottom: 4px;
    width: 30%;
}

.box_changes:hover{
    animation-name: animation1;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    position: relative;
}
@keyframes animation1 {
    0%   {top:0px; bottom: 0;}
    50%  {top:-10px; bottom: -10px;}
  }
.box_changes>div>img{
    margin-top: 20px;
    margin-bottom: 20px;
}
.box_changes>div>h2{
    margin-top: 10px;
    font-size: 20px;
    color: #22c9cf;
    font-family: 'Readex Pro';
    font-weight: bold;
}
.box_changes>div>p{
    font-size: 14px;
    color: #fff;
    margin-bottom: 20px;
    font-family: 'Readex Pro';
}
.box_changes>div>a{
    font-family: 'Readex Pro';
    font-weight: bold;
}
.box_changes>div>a>svg{
    margin-left: 5px;
}


.btn-primary{
    padding: 0px 0px;
    margin-bottom: 27px;
    --bs-btn-color: #c90e87;
    --bs-btn-bg: none;
    --bs-btn-border-color: none;
}
.btn-primary:hover{
    background: none;
    color: #c90e87;
    border-radius: 0;
    border-color: none;
}
.btn-primary{
    background: none;
    color: #c90e87;
    border-radius: 0;
    border-color: none;
}

@media only screen and (max-width: 1000px) {
    .box_changes{
        width: 47%;
    }
    .row_changes{
        display: flex;
    }
  }
@media only screen and (max-width: 800px) {
    .box_changes{
        width: 46%;
    }
    .row_changes{
        display: flex;
    }
  }
@media only screen and (max-width: 560px) {
    .box_changes{
        width: 90%;
    }
    .row_changes{
        display: block;
    }
  }